import { Row } from 'antd';
import React from 'react';
import './Event.css';

function Events() {
  return (
    <div
      style={{
        minHeight: 760,
        backgroundImage: 'linear-gradient(#EECDA3, #EF629F)',
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center',
      }}
    >
      <Row>
        <table>
          <tr>
            <th>Chek</th>
            <th>Num.</th>
            <th>Event</th>
            <th>Date</th>
            <th>Location</th>
          </tr>
          <tr>
            <input type="checkbox" />
            <td>1</td>
            <td>Football</td>
            <td>1401/5/12</td>
            <td>mashhad</td>
          </tr>
          <tr>
            <input type="checkbox" />
            <td>2</td>
            <td>Cycling</td>
            <td>1401/9/2</td>
            <td>tehran</td>
          </tr>
          <tr>
            <input type="checkbox" />
            <td>3</td>
            <td>Volleyball</td>
            <td>1401/5/25</td>
            <td>shiraz</td>
          </tr>
        </table>
      </Row>
    </div>
  );
}

export default Events;
